#projects {
  padding: 50px 0;
  text-align: center;
  background-color: #f9f9f9; /* Sfondo chiaro per un look pulito */
}

.project-container {
  background-color: #ffffff;
  padding: 20px 0;
}

.project-title {
  font-size: 2.5em;
  color: #2c3e50; /* Testo scuro elegante */
  margin-bottom: 20px;
  font-weight: bold;
}

.project-description {
  padding: 10px 20px;
  color: #7f8c8d; /* Grigio raffinato */
  margin-bottom: 40px;
  max-width: 60%;
  margin: 0 auto;
  font-size: 1.2em;
  line-height: 1.6;
}

.project-list-container {
  background-color: #ffffff;
  padding: 50px 0;
}

.project-list {
  display: flex;
  justify-content: center;
  gap: 30px;
  flex-wrap: wrap;
}

.project-card {
  position: relative;
  width: 350px;
  background-color: #ffffff;
  border: 1px solid #ddd;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  cursor: pointer;
}

.project-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

.project-image {
  width: 100%;
  height: auto;
  display: block;
  border-bottom: 2px solid #ddd;
}

.project-details {
  padding: 20px;
  text-align: left;
}

.project-details h3 {
  font-size: 1.8em;
  margin-bottom: 15px;
  color: #2980b9; /* Azzurro brillante */
  font-weight: bold;
}

.project-details p {
  font-size: 1em;
  margin-bottom: 20px;
  color: #34495e; /* Testo elegante */
  line-height: 1.5;
}

.project-button {
  padding: 12px 20px;
  background-color: #3498db; /* Colore azzurro per pulsanti */
  color: #ffffff;
  text-transform: uppercase;
  border: none;
  border-radius: 5px;
  font-size: 1em;
  font-weight: bold;
  cursor: pointer;
  display: block;
  width: 100%;
  text-align: center;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.project-button:hover {
  background-color: #1d6fa5; /* Colore hover più scuro */
  transform: translateY(-5px);
}

.p-project {
  display: flex;
  justify-content: center;
  margin-top: -10px;
  font-size: 1em;
  color: #7f8c8d;
  line-height: 1.6;
}

@media screen and (max-width: 800px) {
  .project-description {
    max-width: 80%;
  }

  .project-list {
    flex-direction: column;
    align-items: center;
  }

  .project-card {
    width: 90%;
  }

  .project-details {
    text-align: center;
  }

  .project-details h3 {
    font-size: 1.5em;
  }

  .project-details p {
    font-size: 0.95em;
  }

  .project-button {
    padding: 10px;
    font-size: 0.9em;
  }
}

@media screen and (max-width: 500px) {
  .project-card {
    width: 100%;
    margin-bottom: 20px;
  }

  .project-details {
    padding: 15px;
  }

  .project-details h3 {
    font-size: 1.3em;
  }

  .project-details p {
    font-size: 0.9em;
  }

  .project-button {
    padding: 8px;
    font-size: 0.8em;
  }
}
