.technologies-section {
  text-align: center;
  color: white;
  background-color: #282c34; /* Sfondo principale */
  padding: 30px 15px;
  padding-bottom: 110px;
}

.technologies-section h2 {
  font-size: 2.5em; /* Titolo maggiore per un impatto visivo */
  margin-bottom: 20px;
  color: #61dafb; /* Colore accattivante */
}

.technologies-description {
  font-size: 1.2em;
  margin-bottom: 30px;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  color: #dcdcdc; /* Testo in grigio chiaro per leggibilità */
}

.technologies-container {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  overflow-x: auto;
  gap: 20px;
}

.technologies-container::-webkit-scrollbar {
  display: none; /* Nasconde la scrollbar */
}

.skill-card {
  background-color: #ffffff; /* Sfondo bianco */
  border: 2px solid #282c34; /* Bordo scuro */
  border-radius: 8px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2); /* Ombra maggiore */
  padding: 20px;
  width: 250px;
  height: 320px; /* Altezza uniforme per consistenza */
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.skill-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3);
  cursor: pointer;
}

.skill-card:hover .icon {
  color: #61dafb;
}

/* Effetti di colore personalizzati per le icone */
.skill-card:hover .icon-redux {
  color: #7749bd;
}

.skill-card:hover .icon-material-ui {
  color: #02f2ff;
}

.skill-card:hover .icon-tailwind {
  color: #38bdf8; /* Blu tipico di Tailwind */
}

.skill-card:hover .icon-visual {
  color: #0076b8;
}

.skill-card:hover .icon-github {
  color: orange;
}

.skill-card .icon {
  font-size: 4em;
  color: #282c34;
  margin-bottom: 15px;
  transition: color 0.3s ease;
}

.skill-card h3 {
  margin-top: 0;
  font-size: 1.6em;
  color: #282c34;
}

.skill-card p {
  color: #666; /* Testo grigio */
  font-size: 1em;
  margin-top: 10px;
  line-height: 1.5; /* Migliora la leggibilità */
}

/* Responsività per schermi medi */
@media (max-width: 800px) {
  .technologies-container {
    flex-direction: column;
    align-items: center;
  }

  .skill-card {
    width: 100%;
    max-width: 300px;
    height: auto; /* Altezza dinamica */
    margin-bottom: 20px;
  }
}

/* Responsività per schermi molto piccoli */
@media (max-width: 430px) {
  .skill-card {
    width: 200px;
    padding: 15px;
      border: none;

  }

  .skill-card .icon {
    font-size: 3.5em;
  }

  .skill-card h3 {
    font-size: 1.4em;
  }

  .skill-card p {
    font-size: 0.9em;
  }
}
