.about-container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 90%;
  background-size: cover;
  background-position: center;
  text-align: center;
  padding: 50px;
}

.about-image {
  background-image: linear-gradient(
      rgba(0, 0, 0, 0.504),
      rgba(0, 0, 0, 0.695)
    ),
    url("../images/programmer.jpg");
  background-size: cover;
  height: 70vh;
}

.overlay {
  position: absolute;
  top: 61px;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Sovrapposizione scura */
  z-index: 1;
}

.about-content {
  position: relative;
  z-index: 1;
  max-width: 800px;
  color: white;
}

.Presentation {
  font-size: 24px;
  margin-bottom: 10px;
  font-weight: 500;
  color: white;
}

.front-end-developer {
  font-size: 32px;
  font-weight: bold;
  margin-top: 10px;
  color: #00bfff; /* Blu vivace e accattivante */
}

.my-description {
  margin-top: 10px;
  font-size: 1.2em;
  color: white;
}

.my-description strong {
  color: #ff9900; /* Colore accattivante per il testo in grassetto */
  font-weight: bold;
}
.cursor {
  font-weight: bold;
  font-size: 32px;
  color: white; /* Imposta il cursore di colore bianco */
}

.blink {
  animation: blink 1s infinite;
}


.curriculum-button {
  display: inline-block;
  padding: 10px 30px;
  margin-top: 20px;
  font-size: 18px;
  color: #fff;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  text-decoration: none;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.curriculum-button:hover {
  background-color: #0056b3;
  transform: scale(1.05);
}

@media screen and (max-width: 800px) {
  .about-container {
    height: 90%;
    padding: 50px 20px;
  }

  .Presentation {
    font-size: 20px;
  }

  .front-end-developer {
    font-size: 26px;
  }

  .my-description {
    font-size: 16px;
  }


  .curriculum-button {
    font-size: 16px;
    padding: 8px 20px;
  }
}

