.footer {
  background-color: #282c34;
  padding: 20px;
  text-align: center;
  margin-bottom: -100px;
}

.social-icons {
  display: flex;
  justify-content: center;
}

.social-icons a {
  margin: 10px 20px;
  font-size: 35px;
  color: #fff;
  transition: color 0.3s ease;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-decoration: none;
}

.social-icons a span {
  font-size: 12px;
  margin-top: 5px;
}

.social-icons a:hover {
  color: #61dafb; 
}

.footer-text {
  margin-top: 30px;
  font-size: 14px;
  color: #fff;
}

