.skill-card {
  background-color: #f0f0f0;
  border: 1px solid #ddd;
  border-radius: 10px;
  padding: 20px;
  margin: 10px;
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  color: black;
}

.skill-card h3 {
  margin-top: 0;
  font-size: 1.5em;
}

.skill-card p {
  font-size: 1em;
  color: #555;
}

.skill-card:hover {
  transform: scale(1.05);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}
