body,
html {
  margin: 0;
  padding: 0;

  overflow-x: hidden;
}

.container {
  width: 100%;
  max-width: 100%;
  overflow: hidden;
  height: 100%;
}

.main-content {
  flex: 1;
  width: 100%;
  padding: 0;
  height: 100%;
}

.footer-container {
  background-color: #282c34;
  padding: 20px;
  text-align: center;
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 100%;
}

.social-icons {
  display: flex;
  justify-content: center;
  margin-bottom: 10px;
}

.social-icons a {
  margin: 0 10px;
  font-size: 24px;
  color: #fff;
  transition: transform 0.3s ease;
}

.social-icons a:hover {
  transform: scale(1.2);
}

.footer-text {
  margin-top: 10px;
  font-size: 14px;
  color: #fff;
}

@media (min-width: 1080px) {
  .container {
    display: block;
  }

  .main-content {
    margin-bottom: 100px;
  }
}
