#skills {
  padding: 60px 20px;
  text-align: center;
  background: linear-gradient(to bottom, #f3f4f6, #e7e8ea); /* Sfondo moderno e sfumato */
  color: #1e293b; /* Colore primario */
}

#skills ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

#skills ul li {
  font-size: 1.2em;
  margin: 10px 0;
  color: #475569; /* Testo in grigio scuro */
}

.skills-section {
  text-align: center;
  margin-bottom: 60px;
}

.skills-section h2 {
  font-size: 2.8em;
  font-weight: 700;
  color: #0f172a; /* Colore forte per il titolo */
  margin-bottom: 20px;
  letter-spacing: 0.5px; /* Leggero spazio tra i caratteri */
}

.skills-description {
  font-size: 1.2em;
  margin-bottom: 40px;
  color: #475569; /* Testo in grigio per leggibilità */
  max-width: 800px;
  margin: 0 auto;
  line-height: 1.8; /* Spaziatura migliorata */
}

.skills-container {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  overflow-x: auto;
  gap: 20px;
  padding: 10px;
}

.skills-container::-webkit-scrollbar {
  display: none; /* Nasconde la scrollbar */
}

.skill-card {
  background: linear-gradient(145deg, #ffffff, #f0f1f3); /* Effetto rilievo */
  border-radius: 12px;
  padding: 20px;
  width: 280px;
  height: 350px; /* Imposta altezza fissa */
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.skill-card:hover {
  transform: translateY(-12px);
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.2);
  border-color: #0f172a; /* Cambia il colore del bordo al passaggio */
  cursor: pointer;
}

.skill-card .icon {
  font-size: 4.2em;
  color: #61dafb; /* Colore React */
  margin-bottom: 15px;
  transition: color 0.3s ease, transform 0.3s ease;
}

.skill-card h3 {
  margin-top: 10px;
  font-size: 1.8em;
  font-weight: 600;
  color: #1e293b;
}

.skill-card p {
  color: #475569; /* Testo grigio scuro */
  font-size: 1em;
  text-align: center;
  line-height: 1.6; /* Migliora la leggibilità */
}

/* Colori delle icone in base alla tecnologia */
.skill-card:hover .icon-html {
  color: #e34f26;
}

.skill-card:hover .icon-css {
  color: #1572b6;
}

.skill-card:hover .icon-javascript {
  color: #f7df1e;
}

.skill-card:hover .icon-react {
  color: #61dafb;
}

.skill-card:hover .icon-redux {
  color: #764abc;
}

.skill-card:hover .icon-tailwind {
  color: #38bdf8;
}

.skill-card:hover .icon-material-ui {
  color: #007fff;
}

.skill-card:hover .icon-visual-studio {
  color: #5c2d91;
}

.skill-card:hover .icon-github {
  color: #181717;
}

@media (max-width: 800px) {
  .skills-container {
    flex-direction: column;
    align-items: center;
  }

  .skill-card {
    width: 100%;
    max-width: 350px;
    height: auto; /* Altezza dinamica per schermi piccoli */
    margin-bottom: 20px;
  }
}

@media (max-width: 400px) {
  .skill-card {
    padding: 15px;
    width: 100%;
    max-width: 300px;
    height: auto; /* Altezza dinamica */
  }

  .skill-card .icon {
    font-size: 3.5em;
  }

  .skill-card h3 {
    font-size: 1.5em;
  }

  .skill-card p {
    font-size: 0.9em;
  }
}
