header {
  background-color: #282c34;
  padding: 10px 0;
  color: white;
  position: fixed;
  width: 100%;
  z-index: 1000;
  transition: background-color 0.3s ease, box-shadow 0.3s ease;
}

header.scrolled {
  background-color: rgba(40, 44, 52, 0.95);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

header div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
}

.logo img {
  max-width: 230px;
  height: auto;
  transition: transform 0.3s ease;
}

header.scrolled .logo img {
  transform: scale(0.9);
}

.nav-menu {
  display: flex;
  align-items: center;
}

.hamburger-menu {
  display: none;
}

.menu-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.33);
  z-index: 999;
}

.menu-items {
  list-style-type: none;
  padding: 0;
  display: flex;
  gap: 20px;
  transition: opacity 0.3s ease;
}

.menu-items a {
  color: white;
  text-decoration: none;
  padding: 10px;
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 5px;
  transition: background-color 0.3s ease, border-color 0.3s ease, color 0.3s ease;
  cursor: pointer;
}

.menu-items a:hover {
  background-color: #61dafb;
  color: #282c34;
}

.menu-items li a {
  font-size: 15px;
  font-weight: bold;
}

@media screen and (max-width: 800px) {
  .hamburger-menu {
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    z-index: 1000;
    background-color: #282c34;
    padding: 5px;
    border-radius: 5px;
  }

  .hamburger-menu img {
    width: 30px;
    height: auto;
  }

  .menu-items {
    position: absolute;
    top: 60px;
    right: 20px;
    background-color: white;
    border-radius: 10px;
    z-index: 999;
    flex-direction: column;
    display: none;
    width: 200px;
    padding: 10px;
    box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
  }

  .menu-items.active {
    display: flex;
    animation: dropdown 0.5s ease forwards;
  }

  .menu-items a {
    color: black;
    border: none;
    transition: none;
  }

  .menu-items li {
    padding: 16px 10px;
    text-align: center;
    margin: 2px 0;
    cursor: pointer;
  }

  .menu-items li:hover {
    background-color: #61dafb;
    border-radius: 5px;
  }

  .menu-items li:hover a {
    color: white;
  }

  .logo img {
    max-width: 200px;
  }
}

.curriculum-button {
  display: inline-block;
  padding: 10px 20px;
  margin-top: 20px;
  background-color: #ff6600;
  color: #ffffff;
  text-decoration: none;
  border-radius: 5px;
  transition: background-color 0.3s;
}

.curriculum-button:hover {
  background-color: #cc5200;
}
